body {
  background-color: rgb(9, 9, 9);
}
#catname {
  color: #f24a20;
  font-size: 30px;
  height: 20px;
  margin-bottom: 30px;
}
#listcatsection {
  position: relative;
  width: 1050px;
  height: auto;
}
#none {
  position: relative;
  height: auto;
  overflow-x: scroll;
  white-space: nowrap;
}
#none {
  position: relative;
  height: auto;
}
#listcatcontainer {
  display: inline-block;
  width: 250px;
  height: 375px;
  margin: 0 3px 3px 3px;
  border-radius: 11px;
  color: #f24a20;
  position: relative;
  transition: all 0.5 linear;
  position: relative;
  background-color: black;
}
#containerimg {
  position: absolute;
  width: 238px;
  height: 300px;
  object-fit: fill;
  border: 1px solid rgb(74, 71, 71);
  border-radius: 10px;
  box-sizing: content-box;
  margin: 1px 5px 5px 2px;
}
#distitle {
  color: white;
  position: absolute;
  width: 80%;
  font-size: larger;
  top: 87%;
  left: 2.5%;
  overflow: hidden;
  text-overflow: ellipsis;
}
#disyear {
  color: white;
  position: absolute;
  font-size: larger;
  top: 87%;
  right: 2.5%;
}
#listcatcontainer:hover {
  border: 2px solid #f24a20;
  z-index: 1;
  background: linear-gradient(to bottom, #f24a20, grey);
  color: black;
}
#videobtn {
  width: 50px;
  position: absolute;
  top: 37.5%;
  left: 40%;
}
::-webkit-scrollbar {
  width: 0;
  height: 0;
}
@media screen and (max-width: 1400px) and (min-width: 1200px) {
  #listcatsection {
    width: 900px;
  }
  #listcatcontainer {
    width: 200px;
  }
  #containerimg {
    width: 185px;
  }
}
